.title {
	line-height: 1;
	@include financier;
	font-size: 48px;
	font-weight: 600;
	color: $blue;
	@include r(767) {
		font-size: 32px; }

	&_sm {
		font-size: 36px;
		@include r(767) {
			font-size: 28px; } }

	&_lg {
		font-size: 58px;
		@include r(767) {
			font-size: 39px; } }

	&_white {
		color: $white; } }
