.form-text {
	&__label {
		position: relative;
		width: 100%;
		padding-bottom: 14px;
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			height: 1px;
			background-color: rgba($gray, 0.2);
			bottom: 0px; } }

	&__label-file {
		&:after {
			display: none; } }

	&__label-text {
		cursor: pointer;
		display: flex;
		text-transform: uppercase;
		color: $blue;
		&:before {
			content: '';
			display: block;
			width: 21px;
			height: 21px;
			margin-right: 10px;
			background: url("../img/clip.svg") no-repeat center; }
		&:hover {
			opacity: .7; } }

	&__label_mt {
		margin-top: 30px; }

	&__input {
		width: 100%;
		padding-left: 10px;
		padding-right: 10px;
		@include markpro;
		font-weight: 400;
		color: $gray;
		font-size: 24px;
		&::placeholder {
			color: $gray; }
		@include r(767) {
			padding: 0; } }

	&__input-file {
		display: none; }

	&__button-row {
		display: flex;
		justify-content: flex-end;
		margin-top: 106px;
		@include r(767) {
			margin-top: 116px; } }

	&__button-row_sm {
		@include r(767) {
			margin-top: 72px; } }

	&__button {} }
